body{
  background-color: black;
  height:100%; 
  overflow-x:hidden;

  
}

p{
  font-family: chanel;
  color: rgba(255, 255, 255, 0.941);
  font-size: 100px;
  padding-left: 540px;
  float: left; 
  padding-top: 220px;
  text-align: center;
  margin: 0;
  display: block;
  

}
.logo{
  background-color: rgba(240, 248, 255, 0);
  border: 0; 
  position: fixed;
  padding-left: 20px;
  padding-bottom: 100px;

}
.logoImg{
  height: 55px;
  position: fixed;
  padding-top: 50px;

}

@media screen and (max-width: 600px) {
  body{
    background-color: black;
    height:auto;
    width: auto;
    overflow:auto;


  }

  p{
    width: 100%;
    font-size: 50px;
    padding: 16px 20px;
    padding-top: 300px;
    
    margin: 0;

  }
  .logo{
    background-color: rgba(240, 248, 255, 0);
    border: 0; 
    position: fixed;

  
  }
  .logoImg{
    height: 40px;
    position: fixed;

  
  }
  
}
@font-face {
  font-family: chanel;
  src: url(font/couture-bld.otf);
}